import { useState } from "react";

function AcademicVisaForm() {

    const [inputs, setInputs] = useState({});
    const [score, setScore] = useState(0);

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        setInputs(values => ({...values, [name]: value}));
        
        setScore(calculateVisaPoints(inputs));
    };

    return (
        <div>
            <div>
                <form>
                    <label>Academic Background:
                        <ul>
                            <li>
                                <label>Doctors Degree</label>
                                <input type="checkbox"
                                value={inputs.hasPHD || false}
                                name="hasPHD"
                                onChange={handleChange}
                                />
                            </li>
                            <li>
                                <label>Masters Degree</label>
                                <input type="checkbox"
                                value={inputs.hasMA || false}
                                name="hasMA"
                                onChange={handleChange}
                                />
                            </li>
                            <li>
                                <label>Bachelors Degree</label>
                                <input type="checkbox"
                                value={inputs.hasBA || false}
                                name="hasBA"
                                onChange={handleChange}
                                />
                            </li>
                            <li>
                                <label>Additional Degrees</label>
                                <input type="checkbox"
                                value={inputs.hasAdditionalDegrees || false}
                                name="hasAdditionalDegrees"
                                onChange={handleChange}
                                />
                            </li>
                        </ul>
                    </label>
                    <br/>
                    <input type="submit" />
                </form>
            </div>
            <div>
                <h2>{score}</h2>
            </div>
        </div>
        
    )
}

function calculateVisaPoints(inputs) {
    let score = 0;
    if ( inputs.hasPHD ){
        score += 30;
    } else if ( inputs.hasMA ) {
        score += 20;
    } else if ( inputs.hasBA ){
        score += 10;
    } else if ( inputs.hasAdditionalDegrees){
        score += 5;
    }
    return score;
  }

  export default AcademicVisaForm;